<template>
    <div
        v-if="Array.isArray(items) && items.length > 0"
        class="rih-cart-component"
    >
        <h3 class="rih-cart-component__heading">
            : {{ format(items[0].time_start) }} -
            {{ format(items[0].time_end) }}
        </h3>
        <div class="rih-cart-component__list">
            <div
                class="rih-cart-component__item rih-cart-item"
                v-for="rent in items"
                :key="rent.inventories[0].id"
            >
                <div
                    v-if="
                        Array.isArray(rent.inventories) &&
                        rent.inventories.length > 0 &&
                        rent.inventories[0].inventory
                    "
                    class="rih-cart-item__inventory"
                >
                    <img
                        class="rih-cart-item__img"
                        :src="MEDIA + rent.inventories[0].inventory.avatar"
                        :alt="rent.inventories[0].inventory.title"
                    />
                    <div class="rih-cart-item__info">
                        <span class="rih-cart-item__title">{{
                            rent.inventories[0].inventory.title
                        }}</span>
                        <span
                            v-if="rent.inventories[0].inventory.category"
                            class="rih-cart-item__category"
                        >
                            {{ rent.inventories[0].inventory.category.title }}
                        </span>
                    </div>
                </div>
                <span class="rih-cart-item__total">
                    : {{ rent.sum }} .
                </span>
                <button class="rih-button rih-button--long"></button>
            </div>
        </div>
        <div class="rih-cart-component__footer">
            <span class="rih-cart-component__total"
                >: {{ 9999 }} .</span
            >
            <button class="rih-button rih-button--long"></button>
        </div>
    </div>
</template>

<script>
import moment from "moment";
import { mapState } from "vuex";
export default {
    name: "CartComponent",
    props: { items: Array },
    computed: {
        ...mapState({
            MEDIA: (state) => state.media,
        }),
    },
    methods: {
        format(date) {
            return moment(date).format("DD.MM.YYYY hh:mm");
        },
    },
};
</script>

<style lang="scss">
#rih-widget .rih-cart-component {
    background-color: var(--widget-background-second);
    padding: 25px;
    box-shadow: 0px 0px 15px 0px rgba(34, 60, 80, 0.1);
    border-radius: 15px;

    &__footer {
        display: flex;
        align-items: center;
        padding: 10px;
        margin-top: 30px;
    }

    &__total {
        margin-left: auto;
        margin-right: 30px;
        font-weight: 700;
    }

    &__list {
        margin-top: 50px;
        padding: 0 40px;
    }

    &__item {
        padding-bottom: 25px !important;
        border-bottom: 2px solid var(--widget-primary-light);
    }

    &__item:last-child {
        border-bottom: none;
        padding-bottom: 10px !important;
    }
}

#rih-widget .rih-cart-item {
    display: flex;
    align-items: center;
    padding: 10px 0;
    margin-top: 20px;
    position: relative;

    &__inventory {
        display: flex;
        align-items: center;

        @media (max-width: 1000px) {
            display: none;
        }

        @media (max-width: 550px) {
            display: none;
        }

        @media (max-width: 800px) {
            display: flex;
        }
    }

    &__img {
        width: 60px;
        height: auto;
    }

    &__info {
        margin-left: 10px;
        & span {
            display: block;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 12px;
        margin-bottom: 3px;
    }

    &__category {
        font-size: 12px;
        text-transform: uppercase;
        color: var(--widget-primary);
        letter-spacing: 1.1px;
        font-weight: 700;
    }

    &__total {
        font-size: 14px;
        font-weight: 700;
        margin-left: auto;
        margin-right: 50px;

        @media (max-width: 400px) {
            font-size: 12px;
        }
    }
}
</style>
