<template>
    <div class="rih-modal rih-modal--smaller" @click="$emit('close')">
        <div class="rih-modal__content" @click.stop>
            <i
                class="rih-modal__icon fa-solid fa-xmark"
                @click="$emit('close')"
            ></i>
            <div class="rih-success-checkmark">
                <div class="check-icon">
                    <span class="icon-line line-tip"></span>
                    <span class="icon-line line-long"></span>
                    <div class="icon-circle"></div>
                    <div class="icon-fix"></div>
                </div>
            </div>
            <div style="text-align: center">
                <p>   </p>
                <button
                    class="rih-button rih-button--long"
                    style="margin-top: 30px"
                    @click="$emit('close')"
                >
                    
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "SuccessModal",
};
</script>

<style lang="scss">
#rih-widget .rih-modal {
    height: 100vh;
    width: 100vw;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 9999;

    &--smaller {
        & .rih-modal__content {
            width: 420px;

            @media (max-width: 420px) {
                width: 100%;
                margin: 0 15px;
            }
        }
    }

    &__content {
        width: 650px;
        background-color: var(--widget-background-second);
        box-shadow: var(--widget-box-shadow-box-2);
        border-radius: 10px;
        padding: 30px 15px;
        position: relative;

        @media (max-width: 650px) {
            width: 100%;
            margin: 0 15px;
        }
    }

    &__icon {
        position: absolute;
        right: 10px;
        top: 10px;
        font-size: 20px;
        color: #000;
        transition: all 0.3s;

        &:hover {
            cursor: pointer;
            color: var(--widget-primary);
        }
    }

    &__header {
        font-size: 24px;
        text-align: center;
        margin-bottom: 40px;
    }

    &__form-group {
        width: 250px;
        margin: 0 auto 14px;
    }

    &__input {
        border-radius: 8px;
        border: 1px solid var(--widget-border-color);
        box-shadow: var(--widget-box-shadow-input);
        padding: 0 10px;
        min-height: 35px;
        font-size: 16px;
        width: 100%;
    }

    &__rent {
        margin-top: 20px;
        text-align: center;
    }

    &__footer {
        display: flex;
        align-items: center;
        margin-top: 40px;
    }

    &__price {
        margin-right: auto;
        margin-left: 30px;
        font-size: 14px;
        font-weight: 700;
    }

    &__inventory {
        display: flex;

        @media (max-width: 500px) {
            display: none;
        }
    }
}

#rih-widget .rih-success-checkmark {
    width: 80px;
    height: 115px;
    margin: 0 auto;

    & .check-icon {
        width: 80px;
        height: 80px;
        position: relative;
        border-radius: 50%;
        box-sizing: content-box;
        border: 4px solid #4caf50;

        &::before {
            top: 3px;
            left: -2px;
            width: 30px;
            transform-origin: 100% 50%;
            border-radius: 100px 0 0 100px;
        }

        &::after {
            top: 0;
            left: 30px;
            width: 60px;
            transform-origin: 0 50%;
            border-radius: 0 100px 100px 0;
            animation: rotate-circle 4.25s ease-in;
        }

        &::before,
        &::after {
            content: "";
            height: 100px;
            position: absolute;
            background: #ffffff;
            transform: rotate(-45deg);
        }

        & .icon-line {
            height: 5px;
            background-color: #4caf50;
            display: block;
            border-radius: 2px;
            position: absolute;
            z-index: 10;

            &.line-tip {
                top: 46px;
                left: 14px;
                width: 25px;
                transform: rotate(45deg);
                animation: icon-line-tip 0.75s;
            }

            &.line-long {
                top: 38px;
                right: 8px;
                width: 47px;
                transform: rotate(-45deg);
                animation: icon-line-long 0.75s;
            }
        }

        & .icon-circle {
            top: -4px;
            left: -4px;
            z-index: 10;
            width: 80px;
            height: 80px;
            border-radius: 50%;
            position: absolute;
            box-sizing: content-box;
            border: 4px solid rgba(76, 175, 80, 0.5);
        }

        & .icon-fix {
            top: 8px;
            width: 5px;
            left: 26px;
            z-index: 1;
            height: 85px;
            position: absolute;
            transform: rotate(-45deg);
            background-color: #ffffff;
        }
    }
}
@keyframes rotate-circle {
    0% {
        transform: rotate(-45deg);
    }
    5% {
        transform: rotate(-45deg);
    }
    12% {
        transform: rotate(-405deg);
    }
    100% {
        transform: rotate(-405deg);
    }
}
@keyframes icon-line-tip {
    0% {
        width: 0;
        left: 1px;
        top: 19px;
    }
    54% {
        width: 0;
        left: 1px;
        top: 19px;
    }
    70% {
        width: 50px;
        left: -8px;
        top: 37px;
    }
    84% {
        width: 17px;
        left: 21px;
        top: 48px;
    }
    100% {
        width: 25px;
        left: 14px;
        top: 45px;
    }
}
@keyframes icon-line-long {
    0% {
        width: 0;
        right: 46px;
        top: 54px;
    }
    65% {
        width: 0;
        right: 46px;
        top: 54px;
    }
    84% {
        width: 55px;
        right: 0px;
        top: 35px;
    }
    100% {
        width: 47px;
        right: 8px;
        top: 38px;
    }
}
</style>
