<template>
    <div
        class="loading"
        :class="{
            'loading--background': background,
            'loading--little': little,
        }"
    >
        <div class="lds-ring">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
        </div>
    </div>
</template>

<script>
export default {
    name: "RihLoading",
    props: {
        background: {
            type: Boolean,
            default: false,
        },
        little: {
            type: Boolean,
            default: false,
        },
    },
};
</script>

<style lang="scss">
#rih-widget .loading {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99;

    & .lds-ring {
        display: inline-block;
        position: relative;
        width: 80px;
        height: 80px;
    }

    & .lds-ring div {
        box-sizing: border-box;
        display: block;
        position: absolute;
        width: 64px;
        height: 64px;
        margin: 8px;
        border: 8px solid var(--widget-primary);
        border-radius: 50%;
        animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
        border-color: var(--widget-primary) transparent transparent transparent;
    }

    & .lds-ring div:nth-child(1) {
        animation-delay: -0.45s;
    }

    & .lds-ring div:nth-child(2) {
        animation-delay: -0.3s;
    }

    & .lds-ring div:nth-child(3) {
        animation-delay: -0.15s;
    }

    &--background {
        background-color: rgba(230, 230, 230, 0.4);
    }

    &--little {
        & .lds-ring {
            width: 50px;
            height: 50px;
        }

        & .lds-ring div {
            width: 40px;
            height: 40px;
            margin: 5px;
            border: 5px solid var(--widget-primary);
            border-color: var(--widget-primary) transparent transparent
                transparent;
        }
    }
}

@keyframes lds-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
</style>
