<template>
    <article class="rih-card">
        <span v-if="inventory.category" class="rih-card__category">
            {{ inventory.category.title }}
        </span>
        <div
            class="rih-card__image"
            :style="{
                'background-image': `url('${MEDIA + inventory.avatar}')`,
            }"
            @click="$emit('pick', inventory, false)"
        ></div>
        <!--        <img-->
        <!--            :src="MEDIA + inventory.avatar"-->
        <!--            :alt="inventory.title"-->
        <!--            class="rih-card__img"-->
        <!--        />-->
        <h3 class="rih-card__heading">{{ inventory.title }}</h3>
        <div class="rih-card__info" v-if="Array.isArray(inventory.option)">
            <span
                class="rih-card__additional"
                v-for="(option, index) in inventory.option"
                :key="index"
            >
                <template v-if="option.title === 'speed'">
                    <i class="fa-solid fa-gauge-high"></i>
                    {{ option.value }} /
                </template>
                <template v-if="option.title === 'boost'">
                    <i class="fa-regular fa-clock"></i>
                    {{ option.value }} 
                </template>
            </span>
            <span class="rih-card__price" v-if="inventory.prices">
                 {{ getMinimumPrice(inventory.prices)
                }}<i class="fa-solid fa-ruble-sign" style="font-size: 12px"></i>
                /
                {{ getMinimumPricePeriod(inventory.prices) }}
            </span>
        </div>
        <div class="rih-card__more" @click="$emit('pick', inventory, false)">
            <span class="rih-card__more-text"></span>
            <i class="fa-solid fa-angle-right"></i>
        </div>
        <div class="rih-card__book" @click="$emit('pick', inventory, true)">
            <span class="rih-card__book-text"></span>
            <i class="fa-solid fa-angle-right"></i>
        </div>
    </article>
</template>

<script>
import humanizeDuration from "humanize-duration";
import moment from "moment";
import { mapState } from "vuex";
export default {
    name: "RihCard",
    props: {
        inventory: Object,
    },
    computed: {
        ...mapState({
            MEDIA: (state) => state.media,
        }),
    },
    methods: {
        getMinimumPrice(prices) {
            let min = -1;
            if (!Array.isArray(prices)) return min;
            prices.forEach((price) => {
                if (min > price.values.slice().reverse()[0].value || min === -1)
                    min = price.values.slice().reverse()[0].value;
            });
            return min;
        },
        getMinimumPricePeriod(prices) {
            let min = -1;
            let period = "";
            if (!Array.isArray(prices)) return period;
            prices.forEach((price) => {
                if (
                    min > price.values.slice().reverse()[0].value ||
                    min === -1
                ) {
                    min = price.values.slice().reverse()[0].value;
                    period = price.values.slice().reverse()[0].period;
                }
            });
            return this.humanize(period);
        },
        humanize(period) {
            const settings = {
                language: "ru",
                delimiter: " ",
                units: ["w", "d", "h", "m"],
            };
            return humanizeDuration(
                moment.duration(period).asMilliseconds(),
                settings
            );
        },
    },
};
</script>
<style lang="scss">
#rih-widget .rih-card {
    width: calc(33.33% - 40px * 2 / 3);
    background-color: var(--widget-background-second);
    padding: 20px 0;
    border-radius: 12px;
    margin-right: 40px;
    margin-bottom: 40px;
    box-shadow: var(--widget-box-shadow-cards);
    position: relative;

    display: flex;
    flex-direction: column;

    &:nth-child(3n) {
        margin-right: 0;
    }

    &__img {
        width: 100%;
        height: auto;
    }

    &__image {
        width: 100%;
        height: 250px;
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        margin-bottom: 8px;
        padding: 0 25px;

        &:hover {
            cursor: pointer;
        }
    }

    &__category {
        color: #aaa;
        position: absolute;
        top: 20px;
        left: 25px;
    }

    &__heading {
        padding: 0 25px;
        margin: 10px 0 5px;
        color: var(--widget-text);
        font-weight: 700;
    }

    &__info {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        font-size: 15px;
        color: var(--widget-text);
        margin-top: 10px;
        margin-bottom: 10px;
        padding: 0 25px;
    }

    &__additional {
        margin-right: 8px;
        margin-bottom: 4px;

        & i {
            margin-right: 6px;
            color: var(--widget-primary);
            font-size: 16px;
        }
    }

    &__more,
    &__book {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 15px 25px;
        cursor: pointer;

        & i {
            position: relative;
            right: 0;
            transition: all 0.5s ease;
        }

        &:hover i {
            right: 8px;
        }
    }

    &__more {
        color: #666;
        border-top: 2px solid #fafafa;
        border-bottom: 2px solid #fafafa;
        margin-top: auto;
    }

    &__book {
        font-weight: 700;
        color: var(--widget-primary);
    }

    @media (max-width: 900px) {
        & {
            width: calc(50% - 40px / 2);
        }
        &:nth-child(3n) {
            margin-right: 30px;
        }
        &:nth-child(2n) {
            margin-right: 0;
        }
    }

    @media (max-width: 590px) {
        & {
            width: 100%;
            margin-right: 0 !important;
        }
    }
}
</style>
