<template>
    <div class="rih-range-filter">
        <date-range-picker
            :key="pickerKey"
            v-model="dateRange"
            auto-apply
            time-picker
            time-picker24-hour
            :ranges="false"
            @update="active = null"
        >
            <template #input="picker">
                <i class="fa-regular fa-calendar"></i>
                <template v-if="picker.startDate && picker.endDate">
                    {{ picker.startDate | formatDate }} -
                    {{ picker.endDate | formatDate }}
                </template>
                <template v-else>   </template>
            </template>
        </date-range-picker>
        <div
            class="rih-range-filter__button"
            :class="{ 'rih-range-filter__button--active': active === 'today' }"
            @click="setToday"
        >
            
        </div>
        <div
            class="rih-range-filter__button"
            :class="{
                'rih-range-filter__button--active': active === 'tomorrow',
            }"
            @click="setTomorrow"
        >
            
        </div>
    </div>
</template>

<script>
import moment from "moment";

export default {
    name: "RangeFilter",
    props: {
        filter: Object,
    },
    data() {
        return {
            dateRange: this.filter,
            active: null,
            pickerKey: "picker",
        };
    },
    watch: {
        dateRange: {
            handler(newVal) {
                this.$emit("update:filter", newVal);
            },
            deep: true,
        },
    },
    filters: {
        formatDate(date) {
            return moment(date).format("DD.MM.YYYY");
        },
    },
    methods: {
        setToday() {
            this.active = "today";
            this.dateRange.startDate = moment().set({ hour: 0, minute: 0 });
            this.dateRange.endDate = moment().set({ hour: 23, minute: 59 });
            // update trick
            this.pickerKey =
                this.pickerKey === "picker" ? "newPicker" : "picker";
        },
        setTomorrow() {
            this.active = "tomorrow";
            this.dateRange.startDate = moment()
                .add(1, "day")
                .set({ hour: 0, minute: 0 });
            this.dateRange.endDate = moment()
                .add(1, "day")
                .set({ hour: 23, minute: 59 });
            // update trick
            this.pickerKey =
                this.pickerKey === "picker" ? "newPicker" : "picker";
        },
    },
};
</script>

<style lang="scss">
#rih-widget.rih-widget .rih-range-filter {
    min-height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    border: 1px solid var(--widget-border-color);
    border-radius: 8px;
    color: var(--widget-text);
    font-size: 15px;
    padding: 0 20px;

    & .form-control.reportrange-text {
        display: inline-flex;
        border: none;
        background-color: var(--widget-background);

        & i {
            margin-right: 12px;
            font-size: 18px;
            color: var(--widget-primary);
        }
    }

    & .calendar-table {
        border: none;
    }

    & .drp-calendar.left {
        margin-right: 20px;
    }

    &__button {
        padding: 5px 20px;
        margin-left: 10px;
        font-size: 13px;

        &:hover {
            cursor: pointer;
        }

        &--active {
            background-color: var(--widget-primary);
            color: #fff;
            border-radius: 8px;
        }
    }
}
</style>
