<template>
    <div class="rih-widget" id="rih-widget" :style="styles">
        <div
            class="rih-widget__list"
            id="rih-list"
            v-if="pickedInventory === null && !showCart"
        >
            <rih-header
                :filter.sync="filter"
                class="rih-widget__header"
                @refresh="loadInventories"
            />
            <rih-list ref="rih-list" :filter="filter" @pick="goInventory" />
        </div>
        <template v-else-if="!showCart">
            <rih-show-page
                class="rih-widget__show-page"
                :picked-inventory="pickedInventory"
                @back="goBack"
            />
            <rih-booking-page
                class="rih-widget__booking"
                :picked-inventory="pickedInventory"
                @back="goBack"
            />
        </template>
        <template v-else>
            <rih-page-cart class="rih-widget__cart" @back="showCart = false" />
        </template>
        <rih-fixed-cart v-if="!showCart" @click="showCart = true" />
        <rih-success-modal
            v-if="showSuccessModal"
            @close="showSuccessModal = false"
        />
    </div>
</template>

<script>
import RihBookingPage from "@/components/BookingPage";
import RihShowPage from "@/components/ShowPage";
import RihList from "@/components/List";
import RihHeader from "@/components/Header";
import RihFixedCart from "@/components/FixedCart";
import RihPageCart from "@/components/PageCart";
import RihSuccessModal from "@/components/SuccessModal";

import pSBC from "@/assets/pSBC";

import { mapState } from "vuex";
import axios from "axios";
export default {
    name: "App",
    props: [
        "tokenApi",
        "devMode",
        "payable",
        "needCart",
        "primaryColor",
        "backgroundColor",
        "backgroundSecondColor",
        "noShadow",
        "noShadowHeader",
        "noShadowCards",
        "noShadowBoxes",
        "noShadowCalendar",
    ],
    components: {
        RihBookingPage,
        RihShowPage,
        RihList,
        RihHeader,
        RihFixedCart,
        RihPageCart,
        RihSuccessModal,
    },
    data() {
        return {
            filter: {
                categories: [],
                points: [],
                dateStart: "",
                dateEnd: "",
            },
            pickedInventory: null,
            showCart: false,
            showSuccessModal: false,
        };
    },
    watch: {
        filter: {
            handler() {
                this.loadInventories();
            },
            deep: true,
        },
    },
    computed: {
        ...mapState({
            TOKEN: (state) => state.token,
            MEDIA: (state) => state.media,
            DOMAIN: (state) => state.domain,
        }),
        styles() {
            const prim = this.primaryColor ?? "#2e55b9";
            const bgc = this.backgroundColor ?? "#fafafa";
            const bgc2 = this.backgroundSecondColor ?? "#ffffff";
            const needShadow = !this.noShadow;
            const needShadowHeader = !this.noShadowHeader;
            const needShadowCards = !this.noShadowCards;
            const needShadowBoxes = !this.noShadowBoxes;
            const needShadowCalendar = !this.noShadowCalendar;
            return {
                "--widget-primary": prim,
                "--widget-primary-hover": pSBC(-0.2, prim),
                "--widget-primary-light": pSBC(0.55, prim),
                "--widget-primary-lightest": pSBC(0.8, prim),
                "--widget-background": bgc,
                "--widget-background-second": bgc2,
                "--widget-box-shadow-input":
                    needShadow && needShadowHeader
                        ? "0px 3px 4px 0px rgba(0, 0, 0, 0.08)"
                        : "none",
                "--widget-box-shadow-cards":
                    needShadow && needShadowCards
                        ? "0px 4px 7px 0px rgba(0, 0, 0, 0.08)"
                        : "none",
                "--widget-box-shadow-box-1":
                    needShadow && needShadowBoxes
                        ? "0px 0px 15px 0px rgba(34, 60, 80, 0.1)"
                        : "none",
                "--widget-box-shadow-box-2":
                    needShadow && needShadowBoxes
                        ? "0px 0px 15px 0px rgba(34, 60, 80, 0.3)"
                        : "none",
                "--widget-box-shadow-calendar":
                    needShadow && needShadowCalendar
                        ? "0px 0px 15px 0px rgba(34, 60, 80, 0.1)"
                        : "none",
            };
        },
    },
    created() {
        this.$store.commit("setToken", this.tokenApi);
        this.$store.commit("setURLs", this.devMode);
        this.$store.commit("setPay", this.payable);
        this.$store.commit("setCart", this.needCart);
        this.$store.commit("setColors", {
            primary: this.primaryColor ?? "#2e55b9",
        });
        this.init();

        let url = new URL(window.location.href);
        let hash = url.searchParams.get("SignatureValue");
        if (hash !== null && hash !== undefined) {
            this.showSuccessModal = true;
            window.history.pushState(
                {},
                document.title,
                window.location.pathname
            );
        }
    },
    methods: {
        init() {
            const link1 = document.createElement("link");
            link1.rel = "preconnect";
            link1.href = "https://fonts.googleapis.com";
            const link2 = document.createElement("link");
            link2.rel = "preconnect";
            link2.href = "https://fonts.gstatic.com";
            link2.setAttribute("crossorigin", "crossorigin");
            const link3 = document.createElement("link");
            link3.rel = "stylesheet";
            link3.href =
                "https://fonts.googleapis.com/css2?family=Manrope:wght@400;700&display=swap";
            const script = document.createElement("script");
            script.src = "https://kit.fontawesome.com/a8314b290c.js";
            script.setAttribute("crossorigin", "anonymous");
            document.head.append(link1);
            document.head.append(link2);
            document.head.append(link3);
            document.head.append(script);
            //this.initStyles();
        },
        initStyles() {
            axios
                .get(
                    "https://api-dev.rentinhand.ru/v1/tilda/widget/css/app2?token=CLMowFporwoWPDsAkKbovdoZNaQEkTfd"
                )
                .then((response) => {
                    if (response.error === undefined && response.data) {
                        const style = document.createElement("style");
                        style.innerHTML = response.data;
                        document.head.append(style);
                    }
                });
        },
        loadInventories() {
            this.$refs["rih-list"].loadInventories();
        },
        goInventory(inv, goToBook) {
            this.pickedInventory = inv;
            this.$nextTick(() => {
                if (goToBook)
                    document.getElementById("rih-booking").scrollIntoView();
                else document.getElementById("rih-showpage").scrollIntoView();
            });
        },
        goBack() {
            this.pickedInventory = null;
            this.$nextTick(() => {
                document.getElementById("rih-list").scrollIntoView();
            });
        },
    },
};
</script>

<style lang="scss">
.rih-widget {
    &__list,
    &__show-page,
    &__booking,
    &__cart {
        padding: 30px 50px;
        background-color: var(--widget-background);

        @media (max-width: 768px) {
            padding: 20px 30px;
        }
    }
}
</style>
