                    </h5>
                    <h2 class="rih-info__heading">
                        {{ pickedInventory.title }}
                    </h2>
                    <h3 class="rih-info__secondary-heading">
                         
                    </h3>
                    <div class="rih-info__features">
                        <span class="rih-info__feature">
                            <i class="fa-solid fa-gauge-high"></i>
                            100 /
                        </span>
                        <span class="rih-info__feature">
                            <i class="fa-solid fa-gauge-high"></i>
                            100 /
                        </span>
                        <span class="rih-info__feature">
                            <i class="fa-solid fa-gauge-high"></i>
                            100 /
                        </span>
                        <span class="rih-info__feature">
                            <i class="fa-regular fa-clock"></i>
                            5.2 
                        </span>
                        <span class="rih-info__feature">
                            <i class="fa-regular fa-clock"></i>
                            5.2 
                        </span>
                        <span class="rih-info__feature">
                            <i class="fa-regular fa-clock"></i>
                            5.2 
                        </span>
                    </div>
                    <div class="rih-info__tariffs">
                        <div class="rih-info__tariff-header">
                            <h3 class="rih-info__secondary-heading">
                                
                            </h3>
                            <span
                                class="rih-info__tariff-heading rih-info__tariff-heading--active rih-info__tariff-heading--ml"
                                >-</span
                            >
                            <span class="rih-info__tariff-heading"
                                >--</span
                            >
                        </div>
                        <div class="rih-info__tariffs-list">
                            <span class="rih-info__tariff-date">
                                1  40 
                            </span>
                            <span class="rih-info__tariff-price">
                                1 800 
                            </span>

                            <span class="rih-info__tariff-date">
                                5  150 
                            </span>
                            <span class="rih-info__tariff-price">
                                7 000 
                            </span>

                            <span class="rih-info__tariff-date">
                                8  200 
                            </span>
                            <span class="rih-info__tariff-price">
                                9 000 
                            </span>

                            <span class="rih-info__tariff-date">
                                  ( 21  11)  150 
                            </span>
                            <span class="rih-info__tariff-price">
                                7 000 
                            </span>

                            <span class="rih-info__tariff-date">
                                   250 
                            </span>
                            <span class="rih-info__tariff-price">
                                10 000 
                            </span>

                            <span class="rih-info__tariff-date">
                                   600 
                            </span>
                            <span class="rih-info__tariff-price">
                                25 000 
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    </div>
</template>

<script>
import humanizeDuration from "humanize-duration";
import moment from "moment";
import { mapState } from "vuex";

export default {
    name: "RihShowpage",
    props: {
        pickedInventory: Object,
    },
    computed: {
        ...mapState({
            TOKEN: (state) => state.token,
            MEDIA: (state) => state.media,
            DOMAIN: (state) => state.domain,
        }),
    },
    methods: {
        humanize(period) {
            const settings = {
                language: "ru",
                delimiter: " ",
                units: ["w", "d", "h", "m"],
            };
            return humanizeDuration(
                moment.duration(period).asMilliseconds(),
                settings
            );
        },
    },
};
</script>

<style lang="scss">
#rih-widget .rih-back {
    justify-content: flex-start !important;
    color: #9a9eb5;

    &__btn {
        cursor: pointer;
    }

    &__rect {
        display: inline-block;
        height: 2px;
        width: 35px;
        background-color: #d9d9d9;
        margin-right: 20px;
        position: relative;
        top: -4px;
    }
}

#rih-widget .rih-info {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;

    &__img-wrapper,
    &__description {
        @media (max-width: 1050px) {
            width: 100%;
        }
    }

    &__img-wrapper {
        width: 30%;

        @media (max-width: 1050px) {
            width: 50%;
            margin-bottom: 30px;
        }

        @media (max-width: 450px) {
            width: 100%;
        }
    }

    &__description {
        background-color: #fff;
        max-width: 600px;
        padding: 40px;
        border-radius: 8px;
    }

    &__img-wrapper img {
        width: 100%;
        height: auto;
        display: block;
        margin: 0 auto;
    }

    &__heading {
        font-weight: 700;
        font-size: 32px;
        margin-top: 0;
        margin-bottom: 22px;
        color: var(--widget-text);
    }

    &__secondary-heading {
        font-size: 20px;
        font-weight: 700;
        margin-top: 0;
        margin-bottom: 20px;
        color: var(--widget-text);
    }

    &__features {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 18px;
    }

    &__feature {
        min-width: 90px;
        max-width: 25%;
        width: 25%;
        margin-bottom: 12px;
        color: var(--widget-text);

        & i {
            color: var(--widget-primary);
        }
    }

    &__tariff-header {
        display: flex;
        align-items: center;
        margin-bottom: 20px;

        & h3 {
            margin: 0;
        }

        @media (max-width: 500px) {
            flex-wrap: wrap;

            & .rih-info__secondary-heading {
                width: 100%;
            }
        }
    }

    &__tariff-heading {
        font-size: 16px;
        font-weight: 700;
        color: #dce3ed;
        cursor: pointer;
        margin-left: 40px;

        &--active {
            color: var(--widget-primary);
            cursor: default;
        }

        &--ml {
            margin-left: auto;

            @media (max-width: 500px) {
                margin-right: auto;
                margin-left: 0;
            }
        }
    }

    &__tariffs-list {
        display: grid;
        grid-template-columns: 1fr auto;
    }

    &__tariff {
        display: flex;
        justify-content: space-between;
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid #dce3ed;
    }

    &__tariff-date {
        color: var(--widget-text);
        font-size: 16px;
    }

    &__tariff-price {
        color: var(--widget-primary);
        font-weight: 700;
        font-size: 16px;
    }

    &__tariff-date,
    &__tariff-price {
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid #dce3ed;

        @media (max-width: 450px) {
            font-size: 14px;
        }

        @media (max-width: 380px) {
            font-size: 12px;
        }
    }

    &__category {
        font-weight: 400;
        font-size: 16px;
        color: #9a9eb5;
        margin-bottom: 5px;
        margin-top: 0;
    }

    &__desc {
        line-height: 1.6;
        font-size: 16px;
    }

    &__price {
        margin-top: 30px;
    }

    &__price--mb {
        margin-bottom: 20px;
    }

    @media (max-width: 1050px) {
        flex-wrap: wrap;

        .info__description {
            margin-top: 36px;
        }
    }
}
</style>
