<template>
    <div class="rih-list">
        <div class="rih-main rih-list__cards">
            <template v-if="!loadingList">
                <rih-card
                    class="rih-main__card"
                    v-for="inventory in inventories"
                    :key="inventory.id"
                    :inventory="inventory"
                    @pick="handlePick"
                ></rih-card>
            </template>
            <rih-loading v-else></rih-loading>
        </div>
        <vue-paginate
            :page-count="totalPages"
            :click-handler="changePage"
            prev-text="<"
            next-text=">"
            container-class="rih-list__pagination"
        >
        </vue-paginate>
    </div>
</template>

<script>
import RihLoading from "@/components/Loading";
import RihCard from "@/components/Card";
import moment from "moment";
import axios from "axios";
import { mapState } from "vuex";

export default {
    name: "RihList",
    components: { RihLoading, RihCard },
    props: {
        filter: {
            type: Object,
            default() {
                return {
                    categories: [],
                    points: [],
                    dateStart: "",
                    dateEnd: "",
                };
            },
        },
    },
    data() {
        return {
            inventories: [],
            loadingList: true,
            total: 0,
            offset: 0,
            limit: 9,
        };
    },
    computed: {
        ...mapState({
            TOKEN: (state) => state.token,
            MEDIA: (state) => state.media,
            DOMAIN: (state) => state.domain,
        }),
        totalPages() {
            return Math.ceil(this.total / this.limit);
        },
    },
    mounted() {
        this.loadInventories();
    },
    methods: {
        loadInventories() {
            this.loadingList = true;
            const filter = {
                include: "option,category,point",
                limit: this.limit,
                offset: this.offset,
                // has_prices: true,
            };
            if (this.filter.categories !== null) {
                filter.category_id = this.filter.categories;
            }
            if (this.filter.points !== null) {
                filter.point_id = this.filter.points;
            }
            if (this.filter.dateEnd !== "" || this.filter.dateStart !== "")
                filter.free_period = {};
            if (this.filter.dateStart !== "") {
                filter.free_period.time_start = moment(this.filter.dateStart);
            }
            if (this.filter.dateEnd !== "") {
                filter.free_period.time_end = moment(this.filter.dateEnd);
            }
            axios
                .post(
                    this.DOMAIN + "/v1/inventory/items?token=" + this.TOKEN,
                    filter
                )
                .then((response) => {
                    if (
                        response.error === undefined &&
                        response.data &&
                        Array.isArray(response.data.array)
                    ) {
                        const data = response.data.array;
                        data.forEach((inv) => {
                            if (inv.option) inv.option = JSON.parse(inv.option);
                        });
                        this.inventories = data;
                        this.total = response.data.option.total;
                    }
                })
                .finally(() => {
                    this.loadingList = false;
                });
        },
        changePage(page) {
            this.offset = page - 1;
            this.loadInventories();
            document.getElementById("rih-widget").scrollIntoView();
        },
        handlePick(inventory, goToBook) {
            this.$emit("pick", inventory, goToBook);
        },
    },
};
</script>

<style lang="scss">
#rih-widget .rih-main {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    min-height: 200px;
    text-align: left;
}

#rih-widget .rih-list {
    text-align: center;

    &__pagination {
        list-style-type: none;
        display: inline-flex;
        border-radius: 8px;
        margin: 25px 0 15px;
        padding-left: 0;
        box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.08);

        & li a {
            background-color: var(--widget-background-second);
            padding: 8px 18px;
            color: #000;
            cursor: pointer;
            border: 2px solid #fff;
            display: inline-block;
            font-size: 16px;

            @media (max-width: 565px) {
                padding: 8px 14px;
            }

            @media (max-width: 495px) {
                padding: 8px 12px;
            }

            @media (max-width: 465px) {
                padding: 6px 10px;
            }

            @media (max-width: 420px) {
                padding: 6px 8px;
            }

            @media (max-width: 385px) {
                font-size: 14px;
            }

            @media (max-width: 370px) {
                padding: 6px;
            }
        }

        & li:first-child a {
            border-top-left-radius: 8px;
            border-bottom-left-radius: 8px;
        }

        & li:last-child a {
            border-top-right-radius: 8px;
            border-bottom-right-radius: 8px;
        }

        & li.disabled a {
            cursor: default !important;
            background-color: var(--widget-background);
            border-color: var(--widget-background);
        }

        & li.active a {
            border-bottom-color: var(--widget-primary);
            color: var(--widget-primary);
        }
    }
}
</style>
