<template>
    <div
        class="rih-cart"
        v-if="needCart && cart.length > 0"
        @click="$emit('click')"
    >
        <i class="fa-solid fa-bag-shopping">
            <span
                class="rih-cart__count"
                :class="{ 'rih-cart__count--smaller': cart.length > 99 }"
            >
                <template v-if="cart.length > 99"> 99+ </template>
                <template v-else>
                    {{ cart.length }}
                </template>
            </span>
        </i>
    </div>
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "FixedCart",
    computed: {
        ...mapState({
            needCart: (state) => state.needCart,
            cart: (state) => state.cart,
        }),
    },
};
</script>

<style lang="scss">
.rih-cart {
    position: fixed;
    bottom: 30px;
    right: 30px;

    background-color: var(--widget-primary);
    color: #fff;
    height: 70px;
    width: 70px;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    box-shadow: 0px 0px 15px 0px rgba(34, 60, 80, 0.3);

    &:hover {
        cursor: pointer;
    }

    & i {
        font-size: 38px;
        position: relative;
    }

    &__count {
        display: flex;
        width: 25px;
        height: 25px;
        justify-content: center;
        align-items: center;
        border-radius: 100%;
        position: absolute;
        right: -8px;
        bottom: -5px;
        font-size: 16px;
        background-color: #f80000;
        color: #fff;

        &--smaller {
            font-size: 12px;
        }
    }
}
</style>
