import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        token: "",
        domain: "",
        media: "",
        payable: "",
        cart: [],
        colors: {},
    },
    mutations: {
        setToken(state, data) {
            state.token = data ? data : "";
        },
        setURLs(state, devMode) {
            state.domain = devMode
                ? "https://api-dev.rentinhand.ru"
                : "https://api.rentinhand.ru";
            state.media = devMode
                ? "https://media-dev.rentinhand.ru"
                : "https://media.rentinhand.ru";
        },
        setPay(state, payable) {
            state.payable = !!payable;
        },
        setCart(state, needCart) {
            state.needCart = !!needCart;
        },
        pushCart(state, element) {
            state.cart.push(element);
        },
        clearCart(state) {
            state.cart = [];
        },
        setColors(state, data) {
            state.colors = data;
        },
    },
    actions: {},
});
