<template>
    <header class="rih-header">
        <h2 class="rih-header__heading"> </h2>
        <div class="rih-header__form-group rih-form-group rih-form-group--left">
            <vue-treeselect
                v-model="localFilter.categories"
                :options="categories"
                :normalizer="normalizer"
                multiple
                searchable
                search-nested
                flatten-search-sesults
                close-on-select
                placeholder=" "
                no-results-text="  "
                no-children-text="  "
                no-options-text=" "
                clear-all-text=" "
                class="rih-form-group__input rih-form-group__class"
            >
                <template #option-label="{ node, shouldShowCount, count }">
                    <label>
                        {{ node.raw.title }}
                        <span v-if="shouldShowCount"> ({{ count }}) </span>
                    </label>
                </template>
                <template #value-label="{ node }">
                    <div>
                        {{ node.raw.title }}
                    </div>
                </template>
            </vue-treeselect>
        </div>
        <div class="rih-header__form-group rih-form-group">
            <rih-range-filter :filter.sync="localFilter" />
        </div>
    </header>
</template>

<script>
import RihRangeFilter from "@/components/RangeFilter";

import axios from "axios";
import { mapState } from "vuex";

export default {
    name: "RihHeader",
    components: { RihRangeFilter },
    props: {
        filter: Object,
    },
    data() {
        return {
            localFilter: this.filter,
            categories: [],
            points: [],
            weekdays: ["", "", "", "", "", "", ""],
            months: [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
            ],
        };
    },
    computed: {
        ...mapState({
            TOKEN: (state) => state.token,
            MEDIA: (state) => state.media,
            DOMAIN: (state) => state.domain,
        }),
    },
    watch: {
        localFilter: {
            handler(newValue) {
                this.$emit("update:filter", newValue);
            },
            deep: true,
        },
    },
    mounted() {
        this.loadCategories();
        this.loadPoints();
    },
    methods: {
        loadCategories() {
            axios
                .post(
                    this.DOMAIN +
                        "/v1/category/inventory/items?token=" +
                        this.TOKEN,
                    {
                        include: "children",
                    }
                )
                .then((response) => {
                    if (
                        response.error === undefined &&
                        response.data &&
                        Array.isArray(response.data.array)
                    ) {
                        this.categories = this.clearEmptyChildren(
                            response.data.array
                        );
                    }
                });
        },
        loadPoints() {
            axios
                .post(this.DOMAIN + "/v1/point/items?token=" + this.TOKEN, {
                    include: "children",
                })
                .then((response) => {
                    if (
                        response.error === undefined &&
                        response.data &&
                        Array.isArray(response.data.array)
                    ) {
                        this.points = response.data.array;
                    }
                });
        },
        isPastDate(date) {
            if (this.filter.dateEnd !== "" && this.filter.dateEnd !== null) {
                return date > new Date(this.filter.dateEnd);
            } else {
                return false;
            }
        },
        isFutureDate(date) {
            if (
                this.filter.dateStart !== "" &&
                this.filter.dateStart !== null
            ) {
                return date < new Date(this.filter.dateStart);
            } else {
                return false;
            }
        },
        clearEmptyChildren(options) {
            if (options) {
                return options.map((option) => {
                    if (
                        option.children !== undefined &&
                        option.children.length === 0
                    ) {
                        delete option.children;
                    } else {
                        option.children = this.clearEmptyChildren(
                            option.children
                        );
                    }
                    return option;
                });
            }
            return options;
        },
        normalizer(node) {
            return {
                id: node.id,
                label: node.title,
                children: node.children,
            };
        },
    },
};
</script>

<style lang="scss">
#rih-widget .rih-header {
    display: flex;
    align-items: center;
    margin-bottom: 40px;

    &__heading {
        font-size: 24px;
        font-weight: 700;
        color: var(--widget-text);
        white-space: nowrap;
        padding: 0;
        margin: 0;
    }

    @media (max-width: 1100px) {
        row-gap: 15px;
        flex-wrap: wrap;
        justify-content: center;
    }
}

#rih-widget .rih-form-group {
    @media (max-width: 600px) {
        width: 100%;
    }

    &--left {
        margin-left: auto;
        margin-right: 20px;

        @media (max-width: 700px) {
            margin-left: 20px;
        }

        @media (max-width: 600px) {
            margin-left: 0;
            margin-right: 0;
        }
    }

    &__class {
        max-width: 250px;

        @media (max-width: 600px) {
            max-width: unset;
        }
    }

    &__input {
        min-height: 40px;
        width: 100%;
    }

    &__inline {
        display: flex;
        align-items: center;
    }

    &__additional {
        margin-right: 10px;

        @media (max-width: 500px) {
            display: none;
        }
    }

    & label {
        display: inline-block;
        font-size: 14px;
        margin-bottom: 2px;
    }
}

#rih-widget .rih-price {
    &__heading {
        font-size: 16px;
        margin-bottom: 8px;
    }

    &__list {
        list-style: none;
    }

    &__item {
        font-size: 15px;
        margin-bottom: 6px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}
</style>
