<template>
    <div v-if="needCart" class="rih-page-cart">
        <header class="rih-header rih-back">
            <button class="rih-button rih-button--long" @click="$emit('back')">
                <i class="fa-solid fa-arrow-left-long"></i>
                
            </button>
        </header>
        <main class="rih-page-cart__main">
            <rih-cart-component
                class="rih-page-cart__component"
                v-for="(value, name) in cartByDate"
                :items="value"
                :key="name"
            />
        </main>
    </div>
</template>

<script>
import RihCartComponent from "@/components/CartComponent";
import { mapState } from "vuex";

export default {
    name: "PageCart",
    components: { RihCartComponent },
    computed: {
        ...mapState({
            needCart: (state) => state.needCart,
            cart: (state) => state.cart,
        }),
        cartByDate() {
            if (!this.cart || this.cart.length === 0) return [];

            const obj = {};
            this.cart.forEach((item) => {
                const key = item.time_start + item.time_end;

                if (!Array.isArray(obj[key])) obj[key] = [];

                obj[key].push(item);
            });

            return obj;
        },
    },
};
</script>

<style lang="scss">
#rih-widget .rih-page-cart {
    &__component {
        margin-bottom: 30px;
    }
}
</style>
